import React from "react";
import "./floating.css";
class Floating extends React.Component {
  render(){
    return(
      <div className="floating"><div className="floatingContent" ref={(div)=>{this.mycontent = div}}>{this.props.children}</div><div className="floatingHolder" ref={(div)=>{this.holder = div;}}>{this.props.children}</div></div>
    );}
}

export default Floating;



