import React, {Component} from "react";
import "./centerbox.css";

class CenterBox extends Component {
  render(){
    return(
      <div className="centerBoxOuter">
        <div className="centerBoxInner">
          {this.props.content}
        </div>
      </div>
    );
  }
}

export default CenterBox;



