import React from "react";
import "./home.css";
class Home extends React.Component {
  render(){
    return(<div className="home"><div className="content"><h1>About Me!</h1>
    <p>I have been coding since I was 15. In addition to knowing MERN stack (Mongo Express Node React) I have also developed in Java and Python, and I am looking to expand my knowledge. My passion is writing good code to make beautiful things.</p>
    <div className="display">
    <img className="java small" src="/images/java.png"/>
    <img className="react big" src="/images/react.png"/>
    <img className="nodejs big" src="/images/nodejs.png"/>
    <img className="python small" src="/images/python.png"/>
    </div></div>
</div>
)};
}
export default Home;



