import React from "react";
import "./sitenav.css"
import "./loginlink.css";
import Floating from "./modules/floating";
import EasyNavList from "./modules/easynavlist";
import autoBind from 'react-autobind';
import {Link} from "react-router-dom";

class SiteNav extends React.Component {
  constructor() {
    super();
    autoBind(this);
    this._isHidden = false;
  }
  isHidden(){
    return this._isHidden;
  }
  componentDidMount() {
    document.getElementById("background").addEventListener('scroll', this.handleScroll);
  }
  handleScroll(event) {
    if (event.srcElement.scrollTop > this.sitenav.offsetHeight) {
      this.hide();
    } else {
      this.show();
    }
  }
  show() {
    this._isHidden = false;
    if (this.sitenav) {
      this.sitenav.style.opacity = 1;
      //this.sitenav.style.visibility = "visible";
      this.sitenav.style.pointerEvents = 'auto';
    }
  }
  hide() {
    this._isHidden = true;
    if (this.sitenav) {
      this.sitenav.style.opacity = 0;
      this.sitenav.style.pointerEvents = 'none';
    }
  }
  render() {
    return (
      <div className="sitenav" ref={(div) => {
        this.sitenav = div;
      }}>
        <Floating>
          <nav className="nav">
            <h1 className="name">Caleb Black</h1>
            <EasyNavList links={[
              [
                "Home", "home"
              ],
              ["github", "https://www.github.com/calebblack"],
              [
                "Art", "art"
              ]
            ]}/>
            <div className="navborder"/>
          </nav>
        </Floating>
      </div>
    );
  }
}
export default SiteNav;



