import React from "react";
import "./easynavlist.css";
import {Link} from "react-router-dom";
class EasyNavList extends React.Component {
  getListItems() {
    return (this.props.links.map(function(link) {
            if (!link[1].startsWith("https://")){
              return <li key={link[0]}><Link className="navlink" to={link[1]}><p className="linkText">{link[0]}</p></Link></li>;
            } else {
              return <li key={link[0]}><a className="navlink" href={link[1]}><p className="linkText">{link[0]}</p></a></li>
            }
        }));
  }
  render() {
    return (
      <ul className="EasyNavList">
        {this.getListItems()}
      </ul>
    );
  };
}
export default EasyNavList



